<template>
  <!--登录-->
  <div class="register">
    <div class="header">
      <van-nav-bar title="用户注册" />
    </div>
    <div class="dlbox">
      <van-cell-group>
        <van-field
          v-model.trim="tel"
          required
          label="手机号"
          type="number"
          :error-message="err1"
          @input="testtel"
          placeholder="请输入手机号"
        />

        <van-field
          v-model.trim="password"
          type="password"
          label="密码"
          placeholder="请输入密码"
          required
          :error-message="err2"
          @input="testmm"
        />
        <van-field
          v-model.trim="password2"
          type="password"
          label="重复密码"
          placeholder="请输入重复密码"
          required
          :error-message="err3"
          @input="testmm2"
        />
        <van-field
          v-model.trim="sms"
          center
          label="短信验证码"
          placeholder="请输入短信验证码"
        >
          <van-button
            slot="button"
            size="small"
            type="primary"
            :disabled="!flag"
            :text="txt"
            @click="yzm"
          ></van-button>
        </van-field>
      </van-cell-group>
      <div class="dlbtn">
        <van-button
          type="primary"
          size="large"
          @click="zhuce"
        >提交</van-button>
      </div>

    </div>

    <tabbar :findex="2" />
  </div>
</template>
<script>
import tabbar from "@/components/tabbar.vue";
export default {
  data() {
    return {
      tel: "",
      password: "",
      password2: "",
      txt: "发送验证码",
      tim: 60,
      flag: true,
      sms: "",
      err1: "",
      err2: "",
      err3:""
    };
  },
  components: {
    tabbar: tabbar
  },
  methods: {
    testtel() { //验证手机号
      if (this.tel.length < 11 || this.tel[0] != "1") {
        this.err1 = "请输入正确的手机号码";
      } else {
        this.err1 = "";
      }
    },
    testmm(){ //验证密码
      if (this.password.length < 6 || this.password.length > 20) {
        this.err2 = "请输入6到20长度的密码";
      } else {
        this.err2 = "";
      }
    },
    testmm2(){ //验证重复密码
      if (this.password2!=this.password) {
        this.err3 = "请输入跟上面相同的密码";
      } else {
        this.err3 = "";
      }
    },
    zhuce() {
      if (this.tel.length < 11 || this.tel[0] != "1") {
        this.err1 = "请输入正确的手机号码";
      } else if (this.password.length < 6 || this.password.length > 20) {
        this.err2 = "请输入6到20长度的密码";
      } else if(this.password2!=this.password){
        this.err3 = "请输入跟上面相同的密码";
      }else if(this.yzm.length==0){
        this.$toast('手机验证码不能为空');
      }
    },
    countDown() {
      //倒计时
      this.flag=false;
      this.tim = this.tim - 1;
      this.txt = this.tim + "秒后重试";
      if (this.tim == 0) {
        this.txt = "发送验证码";
        this.flag = true;
        this.tim = 60;
        return;
      }
      setTimeout(() => {
        this.countDown();
      }, 1000);
    },
    yzm() {
      //发送验证码
      if (this.flag) {
        if (this.tel.length < 11 || this.tel[0] != "1") {
          this.err1 = "请输入正确的手机号码";
        } else {
          this.countDown()
        }
      }
    }
  },
  mounted() {
    //加载完成
  }
};
</script>
<style lang="scss">
.register {
  .van-nav-bar__text,
  .van-nav-bar .van-icon {
    color: #333;
  }
  .dlbox {
    padding-top: 30px;
    .dlbtn {
      padding-top: 10px;
      text-align: center;
      button {
        margin-bottom: 10px;
      }
    }
  }
  .van-cell-group .van-cell {
    background: #fff !important;
    color: #333 !important;
  }
}
</style>

