<template>
  <!--提现-->
  <div class="cash">
    <div class="header">
      <van-nav-bar
        title="提现"
        left-text="返回"
        left-arrow
        @click-left="toback"
      />
    </div>
    <div class="yjbox">
      <center>
        <van-icon
          name="gold-coin"
          color="red"
          size="70px"
        />
        <p class="txt1">￥{{total}}</p>
        <p class="txt2">可提现余额</p>
      </center>
    </div>
    <div class="txbox">
      <van-cell-group>
        <van-field
          type="number"
          v-model="cash"
          label="本次提现"
          placeholder="请输入提现金额"
          @input="luru"
        />

      </van-cell-group>
      <p>提现到账微信号：aaaa</p>
      <p>
        <van-button
          size="large"
          type="primary"
          @click="tixian"
        >去提现</van-button>
      </p>

    </div>
    <van-popup
      v-model="show"
      position="bottom"
      class="mmbox"
      @close="closepop"
    >
      <p>请输入密码</p>
      <van-password-input
        :value="password"
        @focus="showKeyboard = true"
      />
      <van-number-keyboard
        :show="showKeyboard"
        theme="custom"
        @input="onInput"
        @delete="onDelete"
        @blur="showKeyboard = false"
        @close="tijiao"
        close-button-text="完成"
      />
    </van-popup>
    <van-collapse
      v-model="activeName"
      accordion
    >
      <van-collapse-item
        title="提现记录"
        name="1"
      >
        <table
          width="100%"
          border="0"
          cellspacing="0"
          cellpadding="0"
        >
          <tr>
            <td height="30">提现时间</td>
            <td height="30">提现金额</td>
            <td height="30">提现状态</td>
          </tr>
          <tr>
            <td height="30">2016-12-22</td>
            <td height="30">300元</td>
            <td height="30">处理中</td>
          </tr>
          <tr>
            <td height="30">2016-12-22</td>
            <td height="30">300元</td>
            <td height="30">处理中</td>
          </tr>
          <tr>
            <td height="30">2016-12-22</td>
            <td height="30">300元</td>
            <td height="30">成功到账</td>
          </tr>
          <tr>
            <td height="30">2016-12-22</td>
            <td height="30">300元</td>
            <td height="30">成功到账</td>
          </tr>
          <tr>
            <td height="30">2016-12-22</td>
            <td height="30">300元</td>
            <td height="30">成功到账</td>
          </tr>
        </table>
      </van-collapse-item>
    </van-collapse>
    <tabbar :findex="2" />
  </div>
</template>
<script>
import tabbar from "@/components/tabbar.vue";
export default {
  data() {
    return {
      cash: 0,
      total: 9999,
      password: "",
      show: false,
      showKeyboard: false,
      activeName: "1"
    };
  },
  components: {
    tabbar: tabbar
  },
  methods: {
    toback() {
      //返回上一页
      this.$router.go(-1);
    },
    tixian() {
      //提现
      if (this.cash == 0) {
        this.$dialog.alert({
          message: "提现金额不能为零"
        });
      } else if (this.cash > this.total) {
        this.$dialog.alert({
          message: "不能超出提现总额"
        });
      } else {
        this.show = true;
      }
    },
    luru() {
      this.cash = Number(this.cash);
    },
    onInput(key) {
      //键盘输入
      this.password = (this.password + key).slice(0, 6);
    },
    onDelete() {
      //键盘删除
      this.password = this.password.slice(0, this.password.length - 1);
    },
    tijiao() {
      if (this.password.length == 6) {
        alert(this.password);
      } else {
        this.$dialog.alert({
          message: "请输入6位密码"
        });
      }
    },
    closepop(){
      //密码框关闭时清空密码
      this.password=""
    }
  },
  mounted() {
    //加载完成
  }
};
</script>
