<template>
  <!--登录-->
  <div class="login">
    <div class="header">
      <van-nav-bar title="用户登录" />
    </div>
    <div class="dlbox">
      <van-cell-group>
        <van-field
          v-model="tel"
          required
          label="手机号"
          type="number"
          :error-message="err1"
          @input="testtel"
          placeholder="请输入手机号"
        />

        <van-field
          v-model="password"
          type="password"
          label="密码"
          placeholder="请输入密码"
          required
          :error-message="err2"
        />
      </van-cell-group>
      <div class="dlbtn">
        <van-button  type="info" size="large" @click="login">登录</van-button>
        <van-button  plain type="info" size="large">注册</van-button>
        <van-button  type="primary" size="large">微信登录</van-button>
      </div>
      

    </div>

    <tabbar :findex="2" />
  </div>
</template>
<script>
import tabbar from "@/components/tabbar.vue";
export default {
  data() {
    return {
      tel: "",
      password: "",
      err1:"",
      err2:""
    };
  },
  components: {
    tabbar: tabbar
  },
  methods: {
    testtel(){
      if(this.tel.length<11 || this.tel[0]!="1"){
        this.err1="请输入正确的手机号码"
      }else{
        this.err1=""
      }
    },
    login(){
      if(this.tel.length<11 || this.tel[0]!="1"){
        this.$toast.fail('请输入正确的手机号码');
      }else if(this.password.length==0){
        this.$toast.fail('密码不能为空');
      }
    }
  },
  mounted() {
    //加载完成
  }
};
</script>
<style lang="scss">
.login {
  .van-nav-bar__text,
  .van-nav-bar .van-icon {
    color: #333;
  }
  .dlbox{
    padding-top: 30px;
    .dlbtn{
      padding-top: 10px;
      text-align: center;
      button{
        margin-bottom: 10px;
      }
    }
  }
  .van-cell-group .van-cell{
    background: #fff!important;
    color: #333!important;
  }
}
</style>

